import { useState, useEffect } from "react";
import * as sc from "../styled/scHeader";
import { Logo } from "../assets/dynamicAssets";
import RightArrow from "../assets/common/rightArrow.svg";

const Header = () => {
  const [sticky, setSticky] = useState(false);

  const stickyHeaderFn = () => {
    const scrollY = window.scrollY;
    if (scrollY >= 50 && !sticky) setSticky(true);
    else if (scrollY < 50 && sticky) setSticky(false);
  };

  useEffect(() => {
    document.addEventListener("scroll", stickyHeaderFn);
    return () => document.removeEventListener("scroll", stickyHeaderFn);
  });

  return (
    <sc.HeaderWrapper className="header" sticky={sticky}>
      <sc.HeaderContainer>
        <sc.HeaderLogo href="#">
          <Logo />
        </sc.HeaderLogo>
        <sc.HeaderButtonGroup>
          <sc.HeaderButton
            className="header-button"
            href="https://alperenalbayrak.dev"
            target="_blank"
            $outline
            $disabledOnMobile
          >
            Hakkmzda
          </sc.HeaderButton>
          <sc.HeaderButton
            className="header-button"
            href="http://127.0.0.1:8000/accounts/login/"
            target="_blank"
          >
            Giri Yap
            <img src={RightArrow} alt="Giri Yap" />
          </sc.HeaderButton>
        </sc.HeaderButtonGroup>
      </sc.HeaderContainer>
    </sc.HeaderWrapper>
  );
};

export default Header;
